	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"blogrolling.com","date":"09 Oct 2002","slug":"/2002-10-09-blogrollingcom/","tags":"Y2002"},"html":"<p><a href=\"http://blo.gs\">blo.gs</a> has been slow lately. I've noticed a bunch of people now using <a href=\"http://www.blogrolling.com/\">blogrolling.com</a>. It looks like they have added a lot of features that I like, such as chronological sorting and an RSS feed. They update more often, and they seem to have no restriction on how often I pull the feed. So I'm giving them a try. I'm importing their HTML directly for now, rather than groking RSS, but it still doesn't look bad at all.</p>"},"next":{"fields":{"title":"Movable Type 2.5","date":"08 Oct 2002","slug":"/2002-10-08-movable-type-25/"}},"previous":{"fields":{"title":"papascott.com","date":"09 Oct 2002","slug":"/2002-10-09-papascottcom/"}}},{"node":{"fields":{"title":"Movable Type 2.5","date":"08 Oct 2002","slug":"/2002-10-08-movable-type-25/","tags":"Y2002"},"html":"<p>I've moved away from Movable Type, but I'm still happy and excited to see <a href=\"http://www.movabletype.org/25/#about25\">Movable Type 2.5</a>. Congrats to Ben and Mena for creating a product that has made a difference.</p>"},"next":{"fields":{"title":"Twins Win, Warriors Lose","date":"07 Oct 2002","slug":"/2002-10-07-twins-win-warriors-lose/"}},"previous":{"fields":{"title":"blogrolling.com","date":"09 Oct 2002","slug":"/2002-10-09-blogrollingcom/"}}},{"node":{"fields":{"title":"Twins Win, Warriors Lose","date":"07 Oct 2002","slug":"/2002-10-07-twins-win-warriors-lose/","tags":"Y2002"},"html":"<p>Yesterday we spent the day in Eckernförde, visiting with Mama's cousin Rudi visiting from New Zealand. He had left Germany as a boy in the 1950's, and after decades renewed contact with his German relatives, so that we are now seeing him for the 3rd time in 4 years. Mama has a convention in Sydney on her calendar for 2004, so she might get a chance to return the favor. Rudi was regretting missing the <a href=\"http://sport.guardian.co.uk/rugbyleague/story/0,10069,805946,00.html\">National Rugby League final</a> (and will be disappointed that New Zealand lost). But I know how he feels, seeing as the <a href=\"http://www.startribune.com/stories/503/3348777.html\">Twins won their playoff series</a> while I was asleep.</p>"},"next":{"fields":{"title":"Kathy Byrne Caruso","date":"07 Oct 2002","slug":"/2002-10-07-kathy-byrne-caruso/"}},"previous":{"fields":{"title":"Movable Type 2.5","date":"08 Oct 2002","slug":"/2002-10-08-movable-type-25/"}}}],"pathPrefix":"","first":false,"last":false,"index":835,"pageCount":1101}}

